@import url(//fonts.googleapis.com/earlyaccess/jejumyeongjo.css);
@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

.fadeIn { animation: fade-in .5s backwards; }
.fadeOut { animation: fade-out .5s backwards; }

* {
  font-family: 'Jeju Myeongjo', serif;
  box-sizing: border-box;
  transition: all .3s;
  word-break: keep-all;
}

*::selection {
  background-color: rgb(248, 248, 248);
  color: rgb(192, 48, 48);
}

body, p {
  margin: 0;
  padding: 0;
  line-height: 1;
  background: rgb(248, 248, 248);
}
p { background: transparent; }

a { text-decoration: none; }
button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding: 0;
}


/* App */
.App {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  min-height: 100vh;
  justify-content: center;
  font-size: 0.85rem;
  text-align: center;
  margin: 0 auto;
  border-width: 0 2px;
  box-sizing: border-box;
  position: relative;
}

/* dark mode add */
.darkFont { color: #edd !important; }
.darkbg { background: #222 !important; }
.darkInput { 
  border-bottom: 1px solid #edd !important; 
  color: #edd !important;
}
.darkFont::selection, .darkbg::selection, .darkInput::selection {
  background-color: #222 !important;
}

.modal {
  width: 100%;
  height: 300px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  background: rgb(248, 248, 248);
  position: relative;
  box-shadow: 10px 0 10px #ddd, -10px 0 10px #ddd;
  padding: 20px;
  font-size: 0.85rem;
}
.modal > .hint {
  text-align: center;
  margin: 0.75rem 0;
  color: #200;
}
.modalCloseBtn {
  position: absolute;
  top: 15px;
  right: 15px;
  color: red;
  font-size: 2rem;
  height: 32px;
  width: 32px;
  font-weight: 900;
}

/* ReactModal Overlay */
.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}
.ReactModal__Overlay--after-open{
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ReactModal__Overlay--before-close{
  opacity: 0;
}

.header {  
  background: rgba(160, 48, 48);
  color: #fee;
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  font-size: 1rem;
  font-weight: bold;
  position: absolute;
  top: 0;
}
.dummy, .bookmark {
  width: 32px;
  height: 32px;
  border-radius: 10px;
}

/* 화면 전환 애니메이션 */
.solveRiddle-enter {
  background: rgb(192, 48, 48);
  opacity: 1;
}
.solveRiddle-enter-active {
  opacity: 1;
  transition: all 300ms ease-in-out;
}
.solveRiddle-exit {
  opacity: 1;
  background: rgb(192, 48, 48);
}
.solveRiddle-exit-active {
  opacity: 0;
  transition: all 300ms ease-in-out;
}

/* content */
.content {
  display: flex;
  flex-flow: column nowrap;
  /* justify-content: center; */
  align-items: center;
  text-align: left;
  margin-top: 55px;
  padding: 15px;
  width: 100%;
  overflow: auto;
}
.content > .riddleP {
  margin: 1rem 0;
  line-height: 1.5;
  color: #200;
}
.riddleImg {
  max-width: 90%;
  margin: 0 auto;
  display: flex;
  border-radius: 20px;
}
.startBtn, .submitAnswer {
  height: 32px;
  width: 32px;
}
.startBtn > img, .bookmark > img, .submitAnswer > img {
  height: 32px;
  width: 32px;
}


/* footer */
.footer {
  margin-bottom: 20px;
  height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.notice {
  color: rgb(192, 48, 48);
  font-weight: 700;
  min-height: 20px;
}

.answer {
  border: none;
  border-bottom: 1px solid #200;
  background: transparent;
  padding: 5px;
  font-size: 0.9rem;
  /* text-align: center; */
  margin-right: .5rem;
  color: #200;
  width: 120px;
}
.answerContainer {
  display: flex;
  align-items: center;
}


@media all and (min-width: 400px) {
  .App {
    font-size: 0.9rem;
    border-width: 0 3px;
  }

  .modal {
    height: 320px;
    font-size: 1rem;
  }
  .modalCloseBtn {
    top: 25px;
    right: 25px;
    font-size: 2.5rem;
    height: 40px;
    width: 40px;
  }

  .header {  
    height: 70px;
    font-size: 1.2rem;
  }
  .dummy, .bookmark {
    width: 45px;
    height: 45px;
    border-radius: 15px;
  }

  .content {
    margin-top: 70px;
    padding: 25px;
  }
  .startBtn, .submitAnswer {
    height: 42px;
    width: 42px;
  }
  .startBtn > img, .bookmark > img, .submitAnswer > img {
    height: 42px;
    width: 42px;
  }

  .footer {
    height: 70px;
  }
  .answer {
    margin-right: .6rem;
    width: 150px;
  }
}

@media all and (min-width: 800px) {
  .App {
    width: 800px;
    font-size: 1.1rem;
    border-width: 0 3px;
  }

  .modal {
    height: 400px;
    border-radius: 30px;
    font-size: 1.1rem;
    max-width: 500px;
  }

  .header {  
    height: 90px;
    font-size: 2rem;
  }
  .dummy, .bookmark {
    width: 55px;
    height: 55px;
    border-radius: 20px;
  }

  .content {
    margin-top: 90px;
    padding: 40px;
  }

  .startBtn, .submitAnswer {
    height: 55px;
    width: 55px;
  }
  .startBtn > img, .bookmark > img, .submitAnswer > img {
    height: 50px;
    width: 50px;
  }

  .footer {
    height: 80px;
  }

  .answer {
    padding: .5em;
    font-size: 1rem;
    margin-right: .8rem;
    width: 200px;
  }
}